:root {
  --accent: #3b82f6;
  --text-primary: #1f2937;
  --text-secondary: #6b7280;
  --text-muted: #9ca3af;
  --border-light: #e5e7eb;
  --border-focus: var(--accent);
  --bg-white: #ffffff;
  --bg-gray-50: #f9fafb;
  --bg-gray-100: #f3f4f6;
  --shadow-sm: 0 1px 2px rgba(0, 0, 0, 0.05);
  --shadow-md: 0 4px 6px rgba(0, 0, 0, 0.1);
  --radius-sm: 4px;
  --radius-md: 6px;
  --radius-lg: 8px;
  --radius-xl: 12px;
  --spacing-xs: 0.25rem;
  --spacing-sm: 0.5rem;
  --spacing-md: 1rem;
  --spacing-lg: 1.5rem;
  --spacing-xl: 2rem;
}

body {
  background: var(--bg-gray-50);
  color: var(--text-primary);
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, 'Helvetica Neue', Arial, sans-serif;
}

.form-container {
  max-width: 400px;
  margin: 2rem auto;
  padding: var(--spacing-lg);
  box-shadow: var(--shadow-md);
  border-radius: var(--radius-xl);
  background: var(--bg-white);
}

label {
  display: block;
  font-size: 0.875rem;
  font-weight: 500;
  color: var(--text-secondary);
  margin-bottom: 0.25rem;
  letter-spacing: 0.025em;
}

input, textarea, select {
  width: 100%;
  height: 2.5rem;
  padding: 0.5rem;
  border: 1px solid var(--border-light);
  border-radius: var(--radius-md);
  font-size: 1rem;
  transition: all 0.2s ease;
  margin-bottom: var(--spacing-md);
}
input:focus, textarea:focus, select:focus {
  outline: none;
  border-color: var(--border-focus);
  box-shadow: 0 0 0 3px rgba(59, 130, 246, 0.1);
}

.btn-primary {
  background: var(--accent);
  color: white;
  border: 1px solid var(--accent);
  padding: 0.5rem 1rem;
  border-radius: var(--radius-md);
  font-weight: 500;
  cursor: pointer;
  transition: all 0.2s ease;
}
.btn-primary:hover {
  background: rgba(59, 130, 246, 0.9);
  transform: translateY(-1px);
}

.btn-secondary {
  background: transparent;
  color: var(--accent);
  border: 1px solid var(--accent);
  padding: 0.5rem 1rem;
  border-radius: var(--radius-md);
  font-weight: 500;
  cursor: pointer;
  transition: all 0.2s ease;
}
.btn-secondary:hover {
  background: var(--accent);
  color: white;
}

.error {
  border-color: #ef4444 !important;
  color: #dc2626;
}
:disabled {
  opacity: 0.6;
  cursor: not-allowed;
  pointer-events: none;
}

.empty-state {
  text-align: center;
  color: var(--text-muted);
  font-style: italic;
  padding: 2rem;
}

@media (max-width: 360px) {
  .form-container {
    padding: 1rem;
    margin: 0.5rem;
  }
  input, textarea, select {
    padding: 0.375rem;
    font-size: 0.9rem;
  }
}

.card-grid {
  display: flex;
  gap: var(--spacing-lg);
  flex-wrap: wrap;
  justify-content: center;
  align-items: stretch;
  margin-bottom: var(--spacing-xl);
}
.campaign-card {
  background: var(--bg-white);
  border: 1px solid var(--border-light);
  border-radius: var(--radius-lg);
  box-shadow: var(--shadow-sm);
  width: 340px;
  margin: var(--spacing-sm);
  padding: var(--spacing-md);
  display: flex;
  flex-direction: column;
  transition: box-shadow 0.2s;
  position: relative;
  cursor: pointer;
}
.campaign-card:hover {
  box-shadow: var(--shadow-md);
}
.campaign-title {
  font-size: 1.2rem;
  font-weight: 600;
  margin-bottom: var(--spacing-xs);
  overflow-wrap: break-word;
}
.campaign-short-desc {
  font-size: 0.97rem;
  color: var(--text-muted);
  margin-bottom: var(--spacing-sm);
  overflow-wrap: break-word;
}
.campaign-footer {
  margin-top: var(--spacing-sm);
  color: var(--text-secondary);
  display: flex;
  justify-content: space-between;
  align-items: center;
  font-size: 0.9rem;
}
.progress-bar-bg {
  background: var(--bg-gray-100);
  border-radius: var(--radius-lg);
  height: 10px;
  margin-bottom: var(--spacing-sm);
  width: 100%;
  overflow: hidden;
}
.progress-bar-inner {
  background: var(--accent);
  height: 10px;
  border-radius: var(--radius-lg);
  transition: width 0.4s;
}
.status-badge {
  display: inline-block;
  padding: 0.15rem 0.7rem;
  font-size: 0.85rem;
  border-radius: var(--radius-md);
  margin-left: 0.4rem;
  background: var(--bg-gray-100);
  color: var(--text-secondary);
  font-weight: 600;
}
.status-badge[data-status='ACTIVE'] {
  background: #e0f2fe;
  color: #0369a1;
}
.status-badge[data-status='COMPLETED'] {
  background: #bbf7d0;
  color: #166534;
}
.status-badge[data-status='EXPIRED'] {
  background: #fca5a5;
  color: #b91c1c;
}

.donation-list {
  margin-top: var(--spacing-md);
}
.donation-entry {
  background: var(--bg-gray-100);
  border-radius: var(--radius-md);
  padding: var(--spacing-sm);
  margin-bottom: var(--spacing-xs);
  display: flex;
  flex-direction: column;
}
.donation-info-row {
  display: flex;
  justify-content: space-between;
  align-items: baseline;
  margin-bottom: var(--spacing-xs);
}
.donation-message {
  color: var(--text-muted);
  font-size: 0.95rem;
  margin-left: 1.5rem;
  overflow-wrap: break-word;
  margin-bottom: var(--spacing-xs);
}
.details-header {
  margin-bottom: var(--spacing-md);
}

.navbar {
  background: var(--bg-white);
  border-bottom: 1px solid var(--border-light);
  box-shadow: var(--shadow-sm);
  padding: var(--spacing-md) var(--spacing-xl);
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.navbar-logo {
  font-size: 1.3rem;
  font-weight: bold;
  color: var(--accent);
}
.navbar-links {
  display: flex;
  gap: var(--spacing-md);
}
.nav-link {
  text-decoration: none;
  color: var(--text-secondary);
  font-size: 1.05rem;
  transition: color 0.2s;
}
.nav-link.active, .nav-link:hover {
  color: var(--accent);
}
